function getUserInput(recomParameters) {
  const dataJoinKey = recomParameters.dataJoinKey === "" ? undefined : recomParameters.dataJoinKey.split(";").map(key => key.trim())

  const xDomainChange = recomParameters.xScaleDomainDimension === "default" ? undefined : recomParameters.xScaleDomainDimension;
  const yDomainChange = recomParameters.yScaleDomainDimension === "default" ? undefined : recomParameters.yScaleDomainDimension;

  return {
    marks: { marks: { change: { data: dataJoinKey } } },
    scales: {
      x: xDomainChange ? { domainDimension: xDomainChange } : {},
      y: yDomainChange ? { domainDimension: yDomainChange } : {}
    },
    stageN: recomParameters.stageN,
    includeMeta: false, //Todo
    totalDuration: recomParameters.duration || 2000
  }
}

function setUserInput(userInput) {
  let newParams = {
    xScaleDomainDimension: "default",
    yScaleDomainDimension: "default"
  };
  if (userInput && userInput.marks && userInput.marks.marks && userInput.marks.marks.change) {
    newParams.dataJoinKey = (userInput.marks.marks.change.data || []).join(";");
  }

  if (userInput && userInput.scales && userInput.scales.x && userInput.scales.x.domainDimension) {
    newParams.xScaleDomainDimension = userInput.scales.x.domainDimension;
  }

  if (userInput && userInput.scales && userInput.scales.y && userInput.scales.y.domainDimension) {
    newParams.yScaleDomainDimension = userInput.scales.y.domainDimension;
  }

  this.setState({recomParameters: {...this.state.recomParameters, ...newParams}});
}



export {
  setUserInput as setUserInput,
  getUserInput as getUserInput
}