import React, { Component } from  'react';
import AnimContainer from './AnimContainer'
import { debounce } from 'vega';
import { drawVis } from "./util";
import {
  Row, Col
} from 'react-bootstrap'



class VisCol extends Component {
  constructor(props){
    super(props);
    this.debouncedUpdateSVis = debounce(700, this.updateSVis.bind(this));
    this.debouncedUpdateEVis = debounce(700, this.updateEVis.bind(this));
  }

  render() {
    return <Col xs={6}>
      <Row>
        <Col>
        <AnimContainer
          sSpec={this.props.sSpec || {}}
          eSpec={this.props.eSpec || {}}
          gemSpec={this.props.gemSpec || {}}
          showMetaInfo={false}
          id="animView"
        />
        </Col>
      </Row>
      <Row className="mt-3">
        <Col xs={6}>
          <h6>Start Vis</h6>
          <div id="sView"></div>
        </Col>
        <Col xs={6}>
          <h6>End Vis</h6>
          <div id="eView"></div>
        </Col>
      </Row>
    </Col>
  }

  async componentDidUpdate(prevProps){
    if (prevProps.sSpec !== this.props.sSpec) {
      this.debouncedUpdateSVis();
    }
    if (prevProps.eSpec !== this.props.eSpec) {
      this.debouncedUpdateEVis();
    }

  }
  async updateSVis() {
    await drawVis( `#sView`, this.props.sSpec);
  }
  async updateEVis() {
    await drawVis( `#eView`, this.props.eSpec);
  }


}

export default VisCol;
