import * as gemini from "../js/gemini";
import { drawVis } from "./util";
import React, { Component } from  'react';
import { Button, Card } from 'react-bootstrap'
import { debounce } from "vega";
class AnimContainer extends Component {
  constructor(props){
    super(props);
    this.state = {
      status: "ready"
    }
    this.updateAnimViewWithDebounce = debounce(700, this.updateAnimView.bind(this));
  }


  render() {
    let buttonDescription = "Error!",
      controlBtnVariant="danger",
      controlBtnDisabled=true
    if (this.state.status === "ready") {
      buttonDescription = "Play";
      controlBtnVariant = "primary";
      controlBtnDisabled = false;
    } else if (this.state.status === "in progress") {
      buttonDescription = "Playing...";
      controlBtnVariant = "primary";
      controlBtnDisabled = true;
    } else if (this.state.status === "done") {
      buttonDescription = "Reset";
      controlBtnVariant = "secondary";
      controlBtnDisabled = false;
    }
    return <div>
      <div className="text-center" style={{height: this.props.height || 350}}>

        <Button
          variant={controlBtnVariant}
          size="sm"
          className="controlBtn"
          id={`${this.props.id}ControlBtn`}
          data-target-id={`${this.props.id}Anim`}
          onClick={this.onControlBtnClick.bind(this)}
          disabled={controlBtnDisabled}
        >
          { buttonDescription }
        </Button>{
          this.state.hasBuffer ? "*" : ""
        }
        <br/>
        <div style={{overflow: "scroll"}}>
          <div id={`${this.props.id}Anim`} className="mt-3"></div>
          <div id={`${this.props.id}AnimBuffer`} className="mt-3 d-none"></div>
        </div>
      </div>


      {
        this.props.showMetaInfo ?

        <p className="card-text" style={{whiteSpace: "pre-line" }}>
          {
          `Complexity: ${this.props.meta.cost}
          Stage: ${this.props.meta.stageN}`
          }
        </p>
        : ""
      }
    </div>
  }

  async componentDidUpdate(prevProps) {
    if ((prevProps.sSpec !== this.props.sSpec) || (prevProps.eSpec !== this.props.eSpec) || (prevProps.gemSpec !== this.props.gemSpec)) {
      this.updateAnimViewWithDebounce()
    }
  }
  async updateAnimView() {
    // When updating the vis while playing,
    if (this.state.status === "in progress") {
      await drawVis(`#${this.props.id}AnimBuffer`, this.props.sSpec);
      this.setState({hasBuffer: true});
    } else {
      this.setState({status: "ready"});
      await drawVis(`#${this.props.id}Anim`, this.props.sSpec);
    }
  }
  async componentDidMount(){
    this.setState({status: "ready"});
    await drawVis(`#${this.props.id}Anim`, this.props.sSpec);
  }


  async onControlBtnClick(event) {
    let targetElm = event.target;
    if (this.state.status === "ready") {
      // targetElm.setAttribute("disabled", true)
      this.setState({status: "in progress"});
      await this.play.bind(targetElm)(
        this.props.gemSpec,
        this.props.sSpec,
        this.props.eSpec,
        ((anim) => {
          if (this.props.onEnd) {
            this.props.onEnd(anim);
          }

          return true;
        }),
      );

      // targetElm.removeAttribute("disabled");
      if (this.state.hasBuffer) {
        const animDivElm = document.getElementById(this.props.id+"Anim");
        const bufferDivElm = document.getElementById(this.props.id+"AnimBuffer");
        animDivElm.innerHTML = bufferDivElm.innerHTML;
        bufferDivElm.innerHTML = "";
        this.setState({hasBuffer: false, status: "ready"});
      } else {
        this.setState({status: "done"});
      }

    } else if (this.state.status === "done") {
      await this.reset.bind(targetElm)(this.props.sSpec);
      this.setState({status: "ready"});
    }
  }
  async play(gemSpec, sSpec, eSpec, onEnd){
    const targetElm = `#${this.dataset.targetId}`;
    const anim = await gemini.animate(sSpec, eSpec, gemSpec);
    // await drawVis(targetElm, sSpec)
    console.log("Start!");
    await anim.play(targetElm)
    console.log("Done!");
    if (onEnd) {
      onEnd(anim);
    }
    return true;
  }

  async reset(sSpec){
    const targetElm = `#${this.dataset.targetId}`;
    await drawVis( `${targetElm}`, sSpec);
  }
}

export default AnimContainer;


