import React from 'react';
import * as gemini from "./js/gemini";
import { asJSON, objEqual } from './js/util';
import './App.css';
import {loadExamples, setupExample} from './js/init';
import {CodeCol, VisCol, RecomModal, ExampleSelect, GeminiSpecSelect} from './components'
import {getUserInput} from './js/recomControl'

import {
  Container,
  Row,
  Col,
  Button
} from 'react-bootstrap'


class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      recom: {
        sSpec: {},
        eSpec: {}
      },
      sVisEditor: undefined,
      eVisEditor: undefined,
      editor: undefined,
      recomModalShow: false,
      currExample: undefined,
      selectedSpecIndex: 0,
      examples: [],
      recoms: [],
      recomParameters: {
        duration: 2000,
        dataJoinKey: "",
        stageN: 2,
        xScaleDomainDimension: "default",
        yScaleDomainDimension: "default"
      }
    }
  }
  exampleList = [
    `${process.env.REACT_APP_EXAMPLE_PATH}/stimuli/1.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/stimuli/2.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/stimuli/3.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/stimuli/4.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/unstackingBars.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/sortWithFilter.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/line.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/r2d3.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/r2d3-v2.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/democracy.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/scatterPlot.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/introducing-bars.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/addYAxis.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/removeYAxis.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/addLayer.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/removeLayer.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/autoScaleOrder-A.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/autoScaleOrder-B.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/autoScaleOrder-Line.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/staggering.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/enumerator2.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/budget.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/aggregate.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/disaggregate.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/reaggregate.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/barToPoint.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/nbaAllstar.json`,
    `${process.env.REACT_APP_EXAMPLE_PATH}/areaToLine.json`,
  ]
  async updateRecomParameters(paramChange) {
    let newParams = {...this.state.recomParameters, ...paramChange};
    let sSpec = asJSON(this.state.sSpecText) || {},
      eSpec = asJSON(this.state.eSpecText) || {};
    let newRecoms = await gemini.recommend(
      sSpec,
      eSpec,
      getUserInput(newParams)
    );
    this.setState({
      recomParameters: newParams,
      recoms: newRecoms,
      recom: {sSpec, eSpec}
    })
  }
  onSpecChange(specType, newSpecText) {
    if (newSpecText !== this.state[specType+"Text"]) {
      let newSpecState = {};
      newSpecState[specType+"Text"] = newSpecText;
      this.setState(newSpecState);
    }
  }
  onRecomHide() {
    this.setState({recomModalShow: false});
  }
  async showRecom() {
    let sSpec = asJSON(this.state.sSpecText) || {},
      eSpec = asJSON(this.state.eSpecText) || {};
    if (
      (!objEqual(this.state.recom.sSpec, sSpec))
      || (!objEqual(this.state.recom.eSpec, eSpec))
    ) {
      let recoms = await gemini.recommend(
        sSpec,
        eSpec,
        getUserInput(this.state.recomParameters)
      );

      this.setState({
        recoms: recoms,
        recom: {sSpec, eSpec},
        recomModalShow: true
      });
    } else {
      this.setState({ recomModalShow: true });
    }
  }

  render() {
    const noRecom = this.state.currExample ? this.state.currExample.noRecom : false;
    return <div>
      <RecomModal
        modalShow={this.state.recomModalShow}
        onHide={this.onRecomHide.bind(this)}
        recomParameters={this.state.recomParameters}
        recoms={this.state.recoms}
        sSpec={this.state.recom.sSpec}
        eSpec={this.state.recom.eSpec}
        updateRecomParameters={this.updateRecomParameters.bind(this)}
        setValueOnEditor={this.setValueOnEditor.bind(this)}
      />

      <Container fluid>
        <Row className="mt-1">
          <Col>
            <span >Example : </span>
            <ExampleSelect
              examples={this.state.examples}
              setupExample = {setupExample.bind(this)}
              editor={this.state.editor}
              sVisEditor={this.state.sVisEditor}
              eVisEditor={this.state.eVisEditor}
              recomParameters={this.state.recomParameters}
            />


            <GeminiSpecSelect
              gemSpecs={this.state.gemSpecs || []}
              selectedSpecIndex={this.state.selectedSpecIndex}
              updateSelectedSpecIndex={(i) => { this.setState({selectedSpecIndex: i})}}
              onSpecChange={this.onSpecChange.bind(this)}
            />

            <Button
              id="recomBtn"
              size="sm"
              variant="primary"
              onClick={this.showRecom.bind(this)}
              disabled={noRecom}
            >
              💡Recommendation
            </Button>
            <small id="noRecomLabel" className={`text-secondary ${noRecom ? "" : "d-none"}`}>
              This example does not support the recommendation currently.
            </small>
          </Col>
        </Row>
        <Row id="main">

          <CodeCol
            attachEditorRefs={this.attachEditorRefs.bind(this)}
            sSpecText={this.state.sSpecText}
            eSpecText={this.state.eSpecText}
            gemSpecText={this.state.gemSpecText}
            onSpecChange={this.onSpecChange.bind(this)}
          />
          <VisCol
            gemSpec={asJSON(this.state.gemSpecText) || {}}
            sSpec={asJSON(this.state.sSpecText) || {}}
            eSpec={asJSON(this.state.eSpecText) || {}}
          />

        </Row>
      </Container>
    </div>
  }

  async componentDidMount() {
    let loadedExamples = await loadExamples(this.exampleList)


    window.addEventListener("message", (event) => {
      const whitelist = [
        "http://localhost"
      ]


      if (whitelist.find(origin => event.origin.indexOf(origin)>=0) && (typeof(event.data) === "string")) {
        let parsed = asJSON(event.data);
        if (parsed && parsed.sSpec && parsed.eSpec) {
          window.opener.postMessage("ack", "*");
          setupExample.bind(this)(parsed, this.state.editor, this.state.sVisEditor, this.state.eVisEditor);
        }
      }

    })

    this.setState({examples: loadedExamples});
  }

  attachEditorRefs(ref) {
    this.setState(ref);
  }
  setValueOnEditor(str){
    if (this.state.editor) {
      this.state.editor.setValue(str)
    }
  }

}

export default App;
