import React, { Component } from  'react';


class ExampleSelect extends Component {
  constructor(props){
    super(props);
  }


  render() {
    return <select
        id="example-select"
        className="custom-select custom-select-sm mr-1"
        style={{maxWidth: "300px"}}
        onChange={this.onChange.bind(this)}
      >
        <option value="">Select the example</option>
        {
          this.props.examples.map((example, i) => {
            return <option key={i} value={example.value}>{example.title}</option>
          })
        }
      </select>
  }

  onChange(event) {
    let editor = this.props.editor,
      sVisEditor = this.props.sVisEditor,
      eVisEditor = this.props.eVisEditor,
      setupExample = this.props.setupExample;
    let val = event.target.value;
    if (val !== "") {

      setupExample(
        this.props.examples.find(eg => eg.value === val),
        editor, sVisEditor, eVisEditor,
        this.props.recomParameters
      )
    }
  }


}

export default ExampleSelect;
